.burgerMenuIcon {
    right: 24px;
    top: 16px;
    height: 24px;
    width: 24px;
    background-color: transparent;

}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 63px;
    padding: 0 24px;
    background-color: #fff;
    box-shadow: 0 10px 40px rgba(29, 33, 38, 0.13), 0 1px 2px rgba(29, 33, 38, 0.1);
}

.header h1 {
    font-family: 'Montserrat', sans-serif;
    font-size: 22px;
    font-style: normal;
    font-weight: 600;
    line-height: 27px;
    letter-spacing: 0em;
    text-align: left;
}
