.select {
    width: 100%;
    height: 40px;
    border: 1px solid #000;
    border-radius: 5px;
    padding: 5px;
    font-size: 16px;
    outline: none;
    cursor: pointer;
}

.select:focus {
    border: 1px solid #405ac2;

}


.option:hover {
    background-color: #405ac2;
    color: #fff;
}
